.gameButtons {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.gameButtons button {
  padding: 40px 0;
  width: 200px;
  border: 1px solid black;
  text-align: center;
  font-size: 20px;
}
.gameButtons button:hover {
  background-color: azure;
}
.game {
  display: flex;
  flex-direction: row;
  gap: 20px;
}
select input {
  font-size: 15px;
}
input {
  width: 50px;
}
.App {
  margin: 20px;
}

.gameOptions {
  font-size: 20px;
  align-self: center;
  display: flex;
  flex-direction: column;
}

.note {
  display: block;
}

.white {
  padding: 10px;
}

.black {
  background-color: black;
  color: white;
  padding: 0px;
}

.note-container {
  display: flex;
  flex-direction: row;
}